.theme-dark input { color-scheme: dark; }
.theme-light input { color-scheme: light; }


textarea::placeholder,
input::placeholder { color: var(--ui-color-text-1); }


:where(input:not([type=radio],[type=checkbox])) {
	font: inherit;
	height: calc(var(--ui-button-height) - 2px);
	border: 1px solid transparent;
	line-height: 1;
	color: var(--ui-color-text);
	background-color: var(--ui-color-background-input);
	border-radius: var(--ui-border-radius);
	padding: 0 var(--ui-margin-m);
	width: 25ch;
}

:where(input:focus) {
	outline: 0;
	border-color: var(--ui-color-accent);
	box-shadow: var(--ui-shadow-focus);
}

:where(input:invalid) {
	border-color: var(--ui-color-danger);
	box-shadow: var(--ui-shadow-danger);
}


input[type=color] {
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background: none;
	border: none;
	padding: 0;
	cursor: pointer;
}


:where(input:disabled) { opacity: 0.75; cursor: default; }

.input { width: 25ch; vertical-align: middle; }
.input .input-inner {
	display: flex;
	flex-flow: column;
	align-items: stretch;
	justify-content: stretch;
	border-radius: var(--ui-border-radius);
	background: var(--ui-color-background-input);
	border: 1px solid  var(--ui-color-border);
}

.input-inner:focus-within { border-color: var(--ui-color-accent); box-shadow: var(--ui-shadow-focus); }


.input,
.textarea textarea:disabled,
.input select:disabled,
.input input:disabled { opacity: 1; color: var(--ui-color-text); }

.input .input-inner:has(:disabled),
.textarea .textarea-inner:has(:disabled) { opacity: 0.7; }

.input input:not([type=radio]) { width: 100%; border: none; }
.input input:not([type=radio]):focus { box-shadow: none; border: none; }
.input input:not([type=radio]):invalid { border: none; box-shadow: none; }


.input-row {
	width: 100%;
	display: flex;
	flex-flow: row;
	align-items: center;
	justify-content: space-between;
	position: relative;
}

.input-row > .button,
.input-row > .icon {
	position: absolute;
	height: 100%;
	color: var(--ui-color-text-2);
	z-index: 1;
}

.input-row > .icon { left: 7px; width: 1.5rem; pointer-events: none; }
.input-row > .button { right: 2px; }


.theme-light .input-row .icon { color: inherit; }
